*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
nav{
    display: flex;
    justify-content: space-around;
    align-items: center;
    min-height: 10vh;
    background: rgb(189, 199, 195);
    color: white;
}


.nav-links{
    width: 50%;
    display: flex;
    list-style: none;
    justify-content: space-around;
    font-size: large;
    font-weight: 600;
    cursor: pointer;
}
.nav-link{
    color: white;
    text-decoration: none; 
    
}