*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.news-container{
    background-color: wheat;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.news-card{
    display: flex;
    padding: 5vh;
}
.news-card-content{
    margin: 1vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.news-card-content p{
    margin-top: 2px;
}
.news-card-content h5{
    margin-top: 10px;
}