.container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
h1{
    margin: 4vh;
}
.search-container{
    display: flex;
    margin: 4vh;
}
.search-container-input{
    color: red;
    padding: 1vh;
    margin-right: 1vh;
    border-radius: 10px;
    border-style: solid;
    border-color: rgba(0, 0, 0, 0.404);
}
.search-container-button{
    background-color:darkorange;
    color: white;
    padding: 1vh;
    border-radius: 10px;
    border-style: unset;
    border-color: rgba(0, 0, 0, 0.404);
}
img{
    height: 50%;
    margin-bottom: 20px;
}